import { Skeleton } from "@/components/ui/skeleton";

interface PageSkeletonProps {
  showHeader?: boolean;
  showFilters?: boolean;
  children?: React.ReactNode;
}

const PageSkeleton = ({ showHeader = true, showFilters = true, children }: PageSkeletonProps) => {
  return (
    <div className="container mx-auto px-4 py-6 sm:py-8 animate-fade-in">
      {showHeader && (
        <div className="mb-6 sm:mb-8">
          {/* Page title */}
          <Skeleton className="h-8 sm:h-10 w-48 mb-2" />
          {/* Page description */}
          <Skeleton className="h-4 sm:h-5 w-72" />
        </div>
      )}
      
      {showFilters && (
        <div className="flex flex-col sm:flex-row gap-3 sm:gap-4 mb-6">
          {/* Search bar */}
          <Skeleton className="h-10 flex-1 max-w-md rounded-md" />
          {/* Filter buttons */}
          <div className="flex gap-2">
            <Skeleton className="h-10 w-28 rounded-md" />
            <Skeleton className="h-10 w-24 rounded-md" />
          </div>
        </div>
      )}
      
      {children}
    </div>
  );
};

export default PageSkeleton;
