import { useParams, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";
import { AIRecipeSuggestion, useAIRecipe } from "@/hooks/useAIRecipe";
import { useState } from "react";
import { ArrowLeft, Clock, Users, ChefHat, PlayCircle, List, Utensils, Trash2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import RecipeCookingMode from "@/components/community/recipe/RecipeCookingMode";
import { PageSkeleton } from "@/components/skeletons";
import { useSavedRecipes } from "@/hooks/useSavedRecipes";
import { useCreateRecipe } from "@/hooks/useCreateRecipe";
import ResultPhotoStep from "@/components/ai-recipe/ResultPhotoStep";
import PublishConfirmStep from "@/components/ai-recipe/PublishConfirmStep";
import { toast } from "sonner";

type ViewMode = "guided" | "full";
type GuidedPhase = "overview" | "cooking" | "result" | "publish";

const SavedRecipeDetail = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();
  const { user } = useAuth();
  const { deleteRecipe } = useSavedRecipes();
  const createRecipe = useCreateRecipe();
  const { uploadImage, verifyResultPhoto, isVerifying } = useAIRecipe();
  const [viewMode, setViewMode] = useState<ViewMode>("guided");
  const [guidedPhase, setGuidedPhase] = useState<GuidedPhase>("overview");
  const [resultImageUrl, setResultImageUrl] = useState("");
  const [photoVerified, setPhotoVerified] = useState(false);

  const handleCookingComplete = () => {
    setGuidedPhase("result");
  };

  const handlePhotoVerified = (url: string, verified: boolean) => {
    setResultImageUrl(url);
    setPhotoVerified(verified);
    if (verified) {
      setGuidedPhase("publish");
    }
  };

  const handlePublish = async (notes?: string) => {
    if (!recipeData || !resultImageUrl) return;
    try {
      await createRecipe.mutateAsync({
        recipe_name: recipeData.recipe_name,
        subtitle: recipeData.subtitle,
        cook_time: recipeData.cook_time,
        difficulty: recipeData.difficulty,
        servings: recipeData.servings,
        ingredients: recipeData.ingredients,
        steps: recipeData.steps.map((s, i) => ({ ...s, order: i + 1 })),
        cover_image: resultImageUrl,
        introduction: notes?.trim() || recipeData.introduction,
        ai_recipe_source: {
          source: "saved_recipe",
          saved_recipe_id: id,
          result_image: resultImageUrl,
          photo_verified: photoVerified,
        },
      });
      toast.success(t("aiRecipe.allPublished", "È£üË∞±Â∑≤ÂèëÂ∏ÉÂà∞Á§æÂå∫ÔºÅ"));
      navigate("/community");
    } catch {}
  };

  const handleSkipPublish = () => {
    setGuidedPhase("overview");
    toast.success(t("aiRecipe.cookingComplete", "Â§™Ê£í‰∫ÜÔºÅÁÉπÈ•™ÂÆåÊàêÔºÅ"));
  };

  const { data: recipe, isLoading } = useQuery({
    queryKey: ["saved-recipe", id],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("saved_recipes")
        .select("*")
        .eq("id", id!)
        .single();
      if (error) throw error;
      return data as unknown as { id: string; recipe_name: string; recipe_data: AIRecipeSuggestion; created_at: string };
    },
    enabled: !!id && !!user,
  });

  if (isLoading) return <PageSkeleton />;

  if (!recipe) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[50vh] gap-4">
        <p className="text-muted-foreground">{t("aiRecipe.recipeNotFound", "È£üË∞±Êú™ÊâæÂà∞")}</p>
        <Button variant="outline" onClick={() => navigate("/profile")}>
          <ArrowLeft className="h-4 w-4 mr-2" />
          {t("common.back", "ËøîÂõû")}
        </Button>
      </div>
    );
  }

  const recipeData = recipe.recipe_data;

  const getName = () => {
    if (i18n.language === "en" && recipeData.recipe_name_en) return recipeData.recipe_name_en;
    if (i18n.language === "pt" && recipeData.recipe_name_pt) return recipeData.recipe_name_pt;
    return recipeData.recipe_name;
  };

  const getDifficultyColor = (d: string) => {
    switch (d) {
      case "easy": return "bg-green-500/20 text-green-700 dark:text-green-400 border-green-500/30";
      case "medium": return "bg-yellow-500/20 text-yellow-700 dark:text-yellow-400 border-yellow-500/30";
      case "hard": return "bg-red-500/20 text-red-700 dark:text-red-400 border-red-500/30";
      default: return "bg-muted text-muted-foreground";
    }
  };

  const getDifficultyLabel = (d: string) => {
    switch (d) {
      case "easy": return t("recipe.difficulty.easy", "ÁÆÄÂçï");
      case "medium": return t("recipe.difficulty.medium", "‰∏≠Á≠â");
      case "hard": return t("recipe.difficulty.hard", "Âõ∞Èöæ");
      default: return d;
    }
  };

  const steps = recipeData.steps?.map((s, i) => ({
    ...s,
    order: s.order || i + 1,
  })) || [];

  const hasSteps = steps.length > 0;

  const handleDelete = async () => {
    try {
      await deleteRecipe(recipe.id);
      navigate("/profile");
    } catch {}
  };

  const getPageTitle = () => {
    if (guidedPhase === "result") return t("aiRecipe.resultPhotoTitle", "ÊãçÊëÑÊàêÂìÅ");
    if (guidedPhase === "publish") return t("aiRecipe.confirmPublish", "Á°ÆËÆ§ÂèëÂ∏É");
    return recipe.recipe_name;
  };

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <div className="sticky top-14 z-10 bg-background border-b px-4 py-3">
        <div className="flex items-center justify-between max-w-lg mx-auto">
          <button
            onClick={() => {
              if (guidedPhase === "result") setGuidedPhase("cooking");
              else if (guidedPhase === "publish") setGuidedPhase("result");
              else navigate(-1);
            }}
            className="p-1 -ml-1 hover:bg-muted rounded-md"
          >
            <ArrowLeft className="h-5 w-5" />
          </button>
          <h2 className="font-medium text-sm line-clamp-1 flex-1 text-center mx-4">{getPageTitle()}</h2>
          {guidedPhase === "overview" ? (
            <Button variant="ghost" size="icon" className="h-8 w-8 text-muted-foreground hover:text-destructive" onClick={handleDelete}>
              <Trash2 className="h-4 w-4" />
            </Button>
          ) : (
            <div className="w-8" />
          )}
        </div>
      </div>

      <div className="max-w-lg mx-auto p-4">

        {/* Result photo step */}
        {guidedPhase === "result" && (
          <Card className="overflow-hidden">
            <CardContent className="pt-6">
              <ResultPhotoStep
                onPhotoVerified={handlePhotoVerified}
                uploadImage={uploadImage}
                verifyPhoto={verifyResultPhoto}
                recipeName={recipeData.recipe_name}
                isVerifying={isVerifying}
              />
              <div className="mt-4">
                <Button variant="ghost" className="w-full text-muted-foreground" onClick={handleSkipPublish}>
                  {t("aiRecipe.noThanks", "ÊöÇÊó∂‰∏çÂàÜ‰∫´")}
                </Button>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Publish confirmation step */}
        {guidedPhase === "publish" && resultImageUrl && (
          <Card className="overflow-hidden">
            <CardContent className="pt-6">
              <PublishConfirmStep
                resultImageUrl={resultImageUrl}
                recipeName={getName()}
                onPublish={handlePublish}
                onSkip={handleSkipPublish}
                isPublishing={createRecipe.isPending}
              />
            </CardContent>
          </Card>
        )}

        {/* Cooking mode */}
        {guidedPhase === "cooking" && viewMode === "guided" && hasSteps && (
          <Card className="overflow-hidden">
            <RecipeCookingMode
              steps={steps}
              onExit={() => setGuidedPhase("overview")}
              onComplete={handleCookingComplete}
            />
          </Card>
        )}

        {/* Overview / full mode */}
        {(guidedPhase === "overview" || viewMode === "full") && guidedPhase !== "result" && guidedPhase !== "publish" && (
          <>
            {/* Mode toggle */}
            {guidedPhase !== "cooking" && (
              <div className="flex justify-end mb-4">
                <div className="inline-flex h-9 items-center rounded-md bg-muted p-1 text-muted-foreground">
                  <button
                    onClick={() => { setViewMode("guided"); setGuidedPhase("overview"); }}
                    className={`inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5 ${
                      viewMode === "guided" ? "bg-background text-foreground shadow-sm" : ""
                    }`}
                  >
                    <PlayCircle className="h-4 w-4" />
                    {t("recipe.guidedMode", "ÊµÅÁ®ãÊ®°Âºè")}
                  </button>
                  <button
                    onClick={() => setViewMode("full")}
                    className={`inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium transition-all gap-1.5 ${
                      viewMode === "full" ? "bg-background text-foreground shadow-sm" : ""
                    }`}
                  >
                    <List className="h-4 w-4" />
                    {t("recipe.fullMode", "ÂÆåÊï¥Ê®°Âºè")}
                  </button>
                </div>
              </div>
            )}

            <Card className="overflow-hidden">
              <CardHeader className="pb-3">
                <div className="flex items-center gap-2 mb-2">
                  <Badge className="bg-primary text-primary-foreground gap-1">
                    <Utensils className="h-3 w-3" />
                    {t("recipe.badge", "È£üË∞±")}
                  </Badge>
                </div>
                <h2 className="text-xl font-bold">{getName()}</h2>
                <div className="flex flex-wrap items-center gap-3 mt-3">
                  {recipeData.cook_time && (
                    <div className="flex items-center gap-1.5 text-sm text-muted-foreground">
                      <Clock className="h-4 w-4" />
                      <span>{recipeData.cook_time} {t("recipe.minutes", "ÂàÜÈíü")}</span>
                    </div>
                  )}
                  {recipeData.servings && (
                    <div className="flex items-center gap-1.5 text-sm text-muted-foreground">
                      <Users className="h-4 w-4" />
                      <span>{recipeData.servings} {t("recipe.servingsUnit", "‰∫∫‰ªΩ")}</span>
                    </div>
                  )}
                  {recipeData.difficulty && (
                    <Badge variant="outline" className={getDifficultyColor(recipeData.difficulty)}>
                      <ChefHat className="h-3 w-3 mr-1" />
                      {getDifficultyLabel(recipeData.difficulty)}
                    </Badge>
                  )}
                </div>
              </CardHeader>

              <CardContent className="space-y-6">
                {(() => {
                  const intro = i18n.language === "en" && recipeData.introduction_en ? recipeData.introduction_en
                    : i18n.language === "pt" && recipeData.introduction_pt ? recipeData.introduction_pt
                    : recipeData.introduction;
                  return intro && <p className="text-sm text-muted-foreground whitespace-pre-line">{intro}</p>;
                })()}

                <Separator />

                {recipeData.ingredients && recipeData.ingredients.length > 0 && (
                  <div>
                    <h3 className="font-semibold text-sm mb-3">
                      {t("recipe.ingredients", "È£üÊùê")} ({recipeData.ingredients.length})
                    </h3>
                    <div className="flex items-center text-xs text-muted-foreground font-medium px-2 py-1.5 bg-muted/50 rounded-t-md border border-border/50">
                      <span className="flex-1">{t("recipe.ingredientName", "ÂêçÁß∞")}</span>
                      <span className="w-24 text-right">{t("recipe.amount", "Áî®Èáè")}</span>
                    </div>
                    <ul className="border border-t-0 border-border/50 rounded-b-md divide-y divide-border/50">
                      {recipeData.ingredients.map((ing, i) => {
                        const localName = i18n.language === "en" && ing.name_en ? ing.name_en
                          : i18n.language === "pt" && ing.name_pt ? ing.name_pt
                          : ing.name;
                        return (
                          <li key={i} className="flex items-center text-sm px-2 py-2">
                            <span className="flex-1 min-w-0 truncate">{localName}</span>
                            <span className="w-24 text-right text-muted-foreground shrink-0">
                              {ing.amount} {ing.unit}
                            </span>
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                )}

                {viewMode === "guided" && hasSteps && (
                  <Button
                    size="lg"
                    className="w-full gap-2 h-14 text-lg"
                    onClick={() => setGuidedPhase("cooking")}
                  >
                    <PlayCircle className="h-6 w-6" />
                    {t("recipe.startCooking", "ÂºÄÂßãÁÉπÈ•™")}
                    <Badge variant="secondary" className="ml-2">
                      {steps.length} {t("recipe.stepsCount", "Ê≠•")}
                    </Badge>
                  </Button>
                )}

                {viewMode === "full" && hasSteps && (
                  <>
                    <Separator />
                    <div>
                      <h3 className="font-semibold text-sm mb-3">
                        {t("recipe.steps", "Ê≠•È™§")} ({steps.length})
                      </h3>
                      <ol className="space-y-4">
                        {steps.map((step, index) => {
                          const localDesc = i18n.language === "en" && step.description_en ? step.description_en
                            : i18n.language === "pt" && step.description_pt ? step.description_pt
                            : step.description;
                          return (
                            <li key={index} className="flex gap-3">
                              <div className="flex-shrink-0 w-6 h-6 rounded-full bg-primary text-primary-foreground flex items-center justify-center text-xs font-bold">
                                {step.order}
                              </div>
                              <p className="text-sm flex-1">{localDesc}</p>
                            </li>
                          );
                        })}
                      </ol>
                    </div>
                    <Separator />
                    <Button
                      size="lg"
                      className="w-full gap-2 h-14 text-base"
                      onClick={() => setGuidedPhase("result")}
                    >
                      üéâ {t("aiRecipe.finishAndShare", "ÂÆåÊàêÂπ∂ÂàÜ‰∫´Âà∞Á§æÂå∫")}
                    </Button>
                  </>
                )}
              </CardContent>
            </Card>
          </>
        )}
      </div>
    </div>
  );
};

export default SavedRecipeDetail;
